@import '~assets/styles/h3Imports';

.checkbox-element {
  @include input-elements;

  border-radius: 4px;
  height: 24px;
  margin: 1px 0;
  width: 24px;

  &::after {
    background-image: url('../../assets/icons/check-mark.svg');
    content: '';
    height: 12px;
    margin-left: 3px;
    margin-top: 4px;
    position: absolute;
    width: 15px;
  }

  &::before {
    height: 24px;
    width: 24px;
  }

  &:hover:enabled {
    box-shadow: none;
  }

  &:checked::before {
    border-radius: 4px;
  }
}

.shadow {
  height: 24px;
  margin-right: 12px;
  width: 24px;

  &:hover.disabled {
    box-shadow: none;
  }

  &:hover {
    border-radius: 50%;
    box-shadow: 0 0 0 8px var(--input-button-hover, rgba(50, 50, 100, 0.08));
  }
}
